/**
 *
 * Reset Style - Base
 *
 * @author Takuto Yanagida
 * @version 2020-06-18
 *
 */


@mixin form {
	fieldset {
		margin: 0;
		padding: 0;
		border: 0;
	}

	select {
		appearance: none;

		margin: 0;  // for Safari on iOS
		padding: 0;  // for Safari on iOS

		border: 1px solid WindowFrame;
		border-radius: 0;
		color: inherit;
	}

	optgroup {
		font-weight: normal;
		font-style: normal;  // for Firefox
	}

	option, legend {
		padding: 0;
	}

	textarea {
		resize: vertical;

		margin: 0;
		padding: 0;

		border: 1px solid WindowFrame;
		border-radius: 0;
		color: inherit;
	}

	meter, progress {
		vertical-align: baseline;  // for Chrome, Edge, and Firefox.
	}

	// ----

	button, input {
		margin: 0;  // for Checkboxes & radio buttons in all, and buttons in Safari on iOS
		padding: 0;

		border: 1px solid WindowFrame;
		border-radius: 0;
		color: inherit;
	}

	button,
	[type="button" i],
	[type="reset" i],
	[type="submit" i],
	[role="button" i] {
		cursor: pointer;
	}

	button,
	[type="button" i],
	[type="reset" i],
	[type="submit" i] {
		appearance: button;  // Correct the style buttons in Safari on iOS

		&::-moz-focus-inner {
			// Remove the inner border & padding of focus outlines in Firefox
			padding: 0;
			border-style: none;
		}
		&:-moz-focusring {
			// Restore the focus outline styles unset by the previous rule in Firefox
			outline: 1px dotted ButtonText;
		}
	}

	[type="date" i],
	[type="month" i],
	[type="datetime-local" i] {
		appearance: none;  // for Safari on iOS
	}

	[type="search" i] {
		appearance: textfield;  // for Safari on MacOS/iOS
		outline-offset: -2px;  // for Safari on MacOS
	}

	input[type="search" i]::-webkit-search-decoration {  // Specificity is important!
		appearance: none;  // Remove the left padding in Safari on macOS
	}

	input[type="file" i]::-webkit-file-upload-button {  // Specificity is important!
		appearance: button;  // for Chrome & Safari on MacOS/iOS
		font: inherit;

		padding: 0;

		border: 1px solid WindowFrame;
		border-radius: 0;
		color: inherit;
	}

	// ----

	[disabled] {
		cursor: default;
	}
}
